import React, { Component } from 'react';
import Button from "@amzn/meridian/button";
import { Link } from 'react-router-dom';

function callBackend() {
  console.log("Inside call backend");

  fetch("https://dpzd7onwph.execute-api.us-east-1.amazonaws.com/default/MeridianPOC")
    .then(res => {
      console.log("Reponse 1: ", res);
      return res.json()
    })
    .then(
      (result) => {
        console.log("Result: ", result);
        alert(result);
      },
      (error) => {
        console.log("Got some error while calling backend", error);
      }
    );
}

class ScreenOne extends Component {

  componentDidMount() {
    console.log("Inside did mount");
    this.refs.btnDiv.focus();
  }

  onKeyPressed(e) {
    console.log(e.key);
    console.log(e.keyCode);
    alert(e.key);
  }

  render() {
    return (
      <div onKeyPress={this.onKeyPressed} tabIndex="0" ref="btnDiv">
        <div style={{marginTop: "10%"}}>
          <Button
            size="medium"
            minWidth='70%'
          ><Link style={{color: "white"}} to='/screen-two'>Cycle Count [1]</Link></Button>
        </div>

        <div style={{marginTop: "40px"}}>
          <Button onClick={callBackend}
            size="medium"
            minWidth='70%'
          ><Link style={{color: "white"}} onClick={callBackend}>IRDR Count [2]</Link></Button>
        </div>

        <div style={{marginTop: "40px"}}>
          <Button onClick={() => {alert("Clicked Task 3")}}
            size="medium"
            minWidth='70%'
          ><Link style={{color: "white"}} to="/screen-four">Task [3]</Link></Button>
        </div>

        <div style={{marginTop: "40px"}}>
          <Button onClick={() => {alert("Clicked Task 4")}}
            size="medium"
            minWidth='70%'
          ><Link style={{color: "white"}} to="/screen-four">Task [4]</Link></Button>
        </div>
      </div>
    )
  }
}

export default ScreenOne;